.PanelArea {
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.TextArea {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  font-size: 16px;
  color: #333;
  font-weight: bold;
  margin-right: 5px;
}

.RestoreText {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  color: #fa541c;
  font-weight: bold;
  cursor: pointer;
}

.Col {
  display: flex;
  align-items: flex-end;
}

.Parameter {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #333;
  font-weight: bold;
  background-color: #f3f3f3;
  padding: 3px 10px;
  border-radius: 5px;
  margin-left: 20px;
  cursor: pointer;
}
